.person__icon:hover::before {
  opacity: 1;
}

.person__icon:hover .icon {
  color: $torchRed;
}

.portfolio {
  @include e(img) {
    img {
      @include transition(0.3);
    }
  }
}

.portfolio__item:hover img {
  transform: scale(1.1);
}

.portfolio__item:hover::before {
  opacity: 1;
}

.portfolio__link:hover::before {
  opacity: 1;
}

.portfolio__btn {
  @include transition(0.3);
}

.portfolio__btn:hover {
  color: $torchRed;
  opacity: 1;
}

