.portfolio {
  background: $shark;
  @include e(container) {
    padding-top: rem(50px);
    padding-bottom: rem(50px);
  }
  @include e(info) {
    text-align: center;
    margin-bottom: rem(20px);
  }
  @include e(subtitle) {
    @include font-size(14, 14, 1);
    font-weight: $medium;
    color: $torchRed;
    font-family: $secondFontFamily;
  }
  @include e(title) {
    text-transform: capitalize;
    font-size: #{clamp-size(34px, 60px, 320px, 1330px)};
    line-height: 72px;
    font-weight: $bold;
    color: $blueHaze;
  }
  @include e(item) {
    // @include hover-g () {
    //   background: linear-gradient(to right bottom, #212428, #16181c);
    // }
    padding: rem(20px);
    border-radius: rem(20px);
    background: linear-gradient(145deg, #1e2024, #23272b);
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
    @include notlc() {
      margin-bottom: rem(30px);
    }
  }
  @include e(img) {
    position: relative;
    z-index: 100;
    overflow: hidden;
    border-radius: rem(10px);
    @include img();
    margin-bottom: rem(18px);
  }
  @include e(box) {
    text-align: center;
  }
  @include e(name) {
    text-transform: uppercase;
    @include font-size(12, 18, 1);
    color: $torchRed;
    font-weight: $medium;
    margin-bottom: rem(10px);
  }
  @include e(description) {
    color: $blueHaze;
    @include font-size(20, 30);
    font-weight: $semi;
    margin-bottom: rem(20px);
  }
 
  @include e(btn) {
    display: inline-block;
    padding: rem(10px) rem(15px);
    border-radius: rem(8px);
    background: linear-gradient(to right bottom, #393d42, #16181c);
    box-shadow: 2px 3px -9px #1c1e22, -10px -10px 19px #262a2e;
    text-transform: uppercase;
    color: $blueHaze;
  }
}
