// --------------------------------- FLEX --------------------------------- //

@mixin flex () {
    display: flex;
    align-items: center;
}


// --------------------------------- FLEX SPACE BETWEEN--------------------------------- //

@mixin flex-sb() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

// --------------------------------- FLEX CENTER--------------------------------- //

@mixin flex-c() {
  display: flex;
  align-items: center;
  justify-content: center;
}

// --------------------------------- FLEX SPACE AROUND--------------------------------- //

@mixin flex-sa() {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

// --------------------------------- FLEX END--------------------------------- //

@mixin flex-end() {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
