// --------------------------------- RESPONSIVE IMG --------------------------------- //

@mixin img() {
  img {
    max-width: 100%;
    width: 100%;
    display: block;
    height: auto;
  }
}

// --------------------------------- HACK IMG --------------------------------- //

@mixin imgH() {
  & {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
  }
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    @content;
  }
}
