html {
    font-size: $baseFontSize;
  }
  
  html,
  body {
    height: 100%;
    font-family: $baseFontFamily;
    font-size: 1rem;
    font-weight: $regular;
  }
  
  body {
    &.lock {
      overflow: hidden;
    }
  }

  .wrapper {
    @include wrapper();
  }
  
  .main {
    flex: 1 1 auto;
  }
  
  
  a {
    text-decoration: none;
  }
  
  li,
  ul,
  p {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  
  /* ------------------------------------------------------------------ */
  
  [class*="__container"] {
    @include container (1330px);
  }
  