// --------------------------------- FIRST CHILD NOT --------------------------------- //

@mixin notfc($children: true) {
  @if $children or $children == first-child {
    &:not(:first-child) {
      @content;
    }
  }
}

// --------------------------------- LAST CHILD NOT --------------------------------- //

@mixin notlc($children: true) {
  @if $children or $children == last-child {
    &:not(:last-child) {
      @content;
    }
  }
}

// --------------------------------- FIRST CHILD --------------------------------- //

@mixin fch() {
  &:first-child {
    @content;
  }
}

// --------------------------------- LAST CHILD --------------------------------- //

@mixin lch() {
  &:last-child {
    @content;
  }
}

// --------------------------------- NTH-CHILD --------------------------------- //

@mixin nth-child($points) {
  @each $point in $points {
    &:nth-child(#{$point}) {
      @content;
    }
  }
}

@mixin even(){
  &:nth-child(even) {
    @content
  }
}