// --------------------------------- HOVER, FOCUS--------------------------------- //

@mixin hover {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}


@mixin focus {
  &:focus {
    @content;
  }
}


// --------------------------------- HOVER GRADIENT--------------------------------- //

@mixin hover-g () {
  & {
    overflow: hidden;
    position: relative;
    z-index: 1;
  }
  &::before {
    content: "";
    position: absolute;
    opacity: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity .3s ease-in-out;
    z-index: -1;
    @content;
  }
}