.person {
  @include e(container) {
    @include flex-sb();
    flex-direction: row;
  }
  @include e (left) {
    padding: 0 rem(15px);
    order: 0;
    flex: 0 0 55%;
  }
  @include e (text) {
    padding-right: 27%;
  }
  @include e (right) {
    flex: 0 0 45%;
    padding: 0 rem(15px);
  }
}
