// --------------------------------- TRANSITION --------------------------------- //
@mixin transition($tr: 0.24, $name:all) {
  transition: #{$name} #{$tr}s linear;
}

// --------------------------------- CENTER BLOCK --------------------------------- //

@mixin center-block {
  display: block;
  margin: auto;
}


// --------------------------------- ELEMENT --------------------------------- //

@mixin e($element) {
  &__#{$element} {
    @content;
  }
}

// --------------------------------- MODIFIER --------------------------------- //

@mixin m($modifier) {
  &_#{$modifier} {
    @content;
  }
}

// --------------------------------- WRAPPER --------------------------------- //

@mixin wrapper() {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
